@import 'scss/variables/index';

.pd-about {
  margin-bottom: $spacing-2xl;
  padding-top: $spacing-2xl;

  img {
    margin-left: -16px;
    width: calc(100% + 32px);
  }

  &-title {
    color: $primary-color;
    font-size: 60px;
    margin-bottom: $spacing-lg;
  }

  &-content {
    margin-bottom: $spacing-lg;
  }

  .ant-carousel .slick-slide {
    background: $primary-color;
    height: 320px;
    line-height: 320px;
    overflow: hidden;
    text-align: center;
  }

  .ant-carousel .slick-slide h3 {
    color: #fff;
  }
}
