@import 'scss/variables/index';

.pd-introduction {
  background: linear-gradient(#effbfe, #d0e0e7);
  color: $primary-color;
  padding: $spacing-2xl 0;
  min-height: 100vh;

  img {
    width: 100%;
  }

  h1,
  h3 {
    color: $primary-color;
  }

  h1 {
    font-size: 70px;
  }

  h3 {
    font-size: 24px;
    font-weight: 300;
  }

  button {
    background-color: $primary-color;
    border-radius: 25px;
    color: $pd-color-white;
    text-transform: uppercase;

    &:hover,
    &:focus {
      border: 1px solid $primary-color;
      background-color: $primary-color;
      color: $pd-color-white;
    }
  }
}

.pd-product {
  &-title {
    color: $primary-color;
  }

  &-subtitle {
    color: $secondary-color;
  }

  span {
    color: $primary-color;
  }
}

.pd-quality {
  padding: $spacing-2xl 0;

  h1 {
    color: $primary-color;
  }

  &-item {
    display: flex;
  }
}

.pd-traction {
  background: $pd-color-light-gray;
  padding: $spacing-xl 0;
  text-align: center;

  &-title h2 {
    font-weight: bold;
    text-transform: uppercase;
  }

  img {
    max-width: 200px;
    padding: $spacing-lg;
  }
}

.pd-showcase {
  padding: $spacing-2xl 0;

  img {
    width: 100%;
  }

  h1 {
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
  }
}

.pd-product {
  padding: $spacing-2xl 0;

  img {
    width: 100%;
  }

  &-title {
    font-size: 70px;
    font-weight: bold;
  }

  &-subtitle {
    font-size: 24px;
  }

  button {
    border-radius: 25px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      border: 1px solid $primary-color;
      background-color: $pd-color-white;
      color: $primary-color;
    }
  }
}
