@import 'scss/variables/index';

.pd-faq {
  color: $primary-color;
  padding: $spacing-2xl 0;
  min-height: 100vh;

  h1 {
    color: $primary-color;
    font-size: 60px;
  }

  &-divider {
    border-right: 1px solid darkgray;
    width: 1px;
    height: 400px;
  }

  &-wrapper {
    margin-bottom: $spacing-lg;

    &-question {
      font-weight: bold;
      font-size: 20px;
      margin-bottom: $spacing-sm;
    }
  }

  &-extras {
    > div {
      margin-bottom: $spacing-lg;
    }

    &-title {
      font-weight: bold;
      font-size: 20px;
    }
  }
}
