@import 'scss/variables/index';

.pd-products {
  min-height: 100vh;
  padding-top: $spacing-2xl;

  &-divider {
    border-right: 1px solid darkgray;
    height: 360px;
    width: 1px;
  }

  &-title {
    font-size: 60px;
    font-weight: bold;
  }

  &-description {
    color: $pd-color-gray;
  }

  img {
    width: 100%;
  }

  .ant-carousel .slick-slide {
    background: $primary-color;
    height: 320px;
    line-height: 320px;
    overflow: hidden;
    text-align: center;
  }

  .ant-carousel .slick-slide h3 {
    color: #fff;
  }
}
